body {
    background:#2C3E50;
  }
  
  * {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
  }
  
  .main-section {
    background: transparent;
    max-width: 800px;
    width: 90%;
    height: 500px;
    margin: 30px auto;
  }
  
  .clock-holder {
    width: 100%;
    background: #5B85AA;
    margin: 30px auto;
    position: relative;
  }
  
  
  .stopwatch {
    margin-top: 120px;
    padding: 60px 0px;
    text-align: center;
  }
  .stopwatch span{
    background:#fff;
    color: F79D65;
    display: inline-block;
    font-family: monospace;
    font-size: 22px;
    padding: 20px;
    border-radius: 5px;
    width: 70px;
  }
  
  .stopwatch .stopwatch-btn {
    background: white;
    margin-top: 30px;
    margin-left: 5px;
    border: none;
    padding: 12px 20px;
    font-size: 16px;
    text-transform: uppercase;
    width: 150px;
    transition: background 1s;
    cursor: pointer;
  }
  .stopwatch .stopwatch-btn-red{
    border: 2px solid #C85454;
    color: #C85454;
  }
  .stopwatch .stopwatch-btn-yel{
    border: 2px solid #FFC900;
    color: #FFC900;
  }
  
  .stopwatch .stopwatch-btn-gre{
    border: 2px solid #F79D65;
    color: #F79D65;
  }
  .stopwatch .stopwatch-btn-red:hover {
    background: #C85454;
    border: 2px solid #C85454;
    color: #fff;
  }
  .stopwatch .stopwatch-btn-yel:hover {
    background: #FFC900;
    border: 2px solid #FFC900;
    color: #fff;
  }
  .stopwatch .stopwatch-btn-gre:hover {
    background: #F79D65;
    border: 2px solid #F79D65;
    color: white;
  }

  .header {
      border: black 2px solid;
      border-radius: 30px;
      background: white;
      width: 50%;
      margin: auto;
      margin-bottom: 20px;
  }