.home-main-container {
	/* outline: orangered solid 2px; */

	margin: 100px 10px;
}

.app-btn-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.single-btn-containers {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;

	width: 100%;
	height: 300px;
}

.link-btn {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	/* 
	outline: rgb(0, 100, 214) solid 2px; */
	background-color: #f79d65;
	border-radius: 20px;

	transition: all 0.25s ease-in-out;
}

.link-btn:hover {
	cursor: pointer;
	/* background-color: #e48950; */
	background-color: #5b85aa;
}

.link-btn svg {
	padding: 30px;

	fill: #1e2139;
	height: 300px;
	/* outline: orangered solid 2px; */
}

.link-text {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;

	font-size: 30px;
	font-weight: bold;
}

/* use minwidth */

@media screen and (max-width: 69420px) {
	.single-btn-containers,
	.link-btn svg {
		/* font-size: 50px; */
		height: 400px;
	}
}

@media screen and (max-width: 1875px) {
	.single-btn-containers,
	.link-btn svg {
		height: 300px;
	}
}

@media screen and (max-width: 1450px) {
	.single-btn-containers,
	.link-btn svg {
		height: 275px;
	}
}

/* mobile and tablets */

@media screen and (max-width: 1300px) {
	.single-btn-containers,
	.link-btn svg {
		height: 315px;
	}
}

@media screen and (max-width: 1300px) {
	.app-btn-container {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}
}

@media screen and (max-width: 1300px) {
	.single-btn-containers {
		margin-bottom: 75px;
	}
}

